<template>
  <div class="reservation-list-container">
    <van-checkbox-group v-model="result" @click="toggle(index)">
      <div class="my-line">第{{ index + 1 }}项</div>
      <van-form readonly colon label-width="7em">
        <van-field
          :label="labelObj.labelOne"
          :value="item.listNumber"
          readonly
        />
        <van-field
          :label="labelObj.labelTwo"
          :value="item.projectName"
          readonly
        >
          <template #input>
            <div class="my-box">
              <span>{{ item.projectName }}</span>
            </div>
          </template>
          <template #right-icon v-if="showCheck">
            <van-checkbox :name="item" ref="checkboxes" shape="square" />
          </template>
          <template #button >
            <van-button v-if="showButton" class="main-bg-color" type="info" size="mini"
              >修改</van-button
            >
            <van-button plain  v-if="showCancleButton"   size="mini"
              >取消预约</van-button
            >
          </template>
        </van-field>
        <van-field :label="labelObj.labelThree" :value="item.date" readonly />
      </van-form>
      <van-divider />
    </van-checkbox-group>
  </div>
</template>

<script>
export default {
  props: {
    showCheck: {
      type: Boolean,
      default: false,
    },
    showButton: {
      type: Boolean,
      default: false,
    },
    showCancleButton:{
        type: Boolean,
      default: false,
    },
    labelObj: {
      type: Object,
    },
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
  },
  data() {
    return {
      result: [],
    };
  },
  methods: {
    toggle(index) {
      console.log(index);
      this.$refs.checkboxes[index].toggle();
    },
  },
};
</script>

<style scoped lang="scss">
.reservation-list-container {
  margin-bottom: 20px;
  .my-line {
    text-align: left;
    margin-left: 12px;
    padding-left: 5px;
    border-left: 1px solid rgb(0, 195, 255);
  }
  .my-box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-right: 4em;
  }
  .van-cell {
    padding: 0 16px;
    &::after {
      border: none;
    }
  }
}
</style>