<template>
  <div class="top-nav-container">
    <van-nav-bar
      :title="activeRouterName"
      left-arrow
      @click-left="goToRouter"
    />
  </div>
</template>

<script>
export default {
  props: {
    activeRouterName: {
      type: String,
    },
    originRouterName: {
      type: String,
    },
  },
  methods: {
    goToRouter() {
      this.$router.push({
        name: this.originRouterName,
      });
    },
  },
};
</script>

<style  scoped>
.top-nav-container {
}
</style>